	export default {
		data() {
			return {
				tabList: ['快递配送', '上门自提'],
        tabDefault: 0,                     // 快递或者自提  
        shop_id: 0,                        // 自提门店ID
        option: {},                        // 接受到的参数
        delivery: 10,                      // 快递10 自提20
        detail: {},                        // 订单数据
        remark: '',                        // 买家留言
			};
		},
    onLoad(option) {
      this.option= JSON.parse(option.data)
      console.log(this.option)
    },
    onShow() {
      // 提交订单
      this.orderBuy()
    },
    watch: {
      tabDefault(val, oldval) {
        if(val === 1) {
          this.delivery= 20
        } else {
          this.delivery= 10
        }
        this.orderBuy()
      }
    },
    methods: {
      selectTab(index) {
        this.tabDefault= index
      },
      // 提交订单
      orderBuy() {
        let that = this
        let data = that.option
        data.delivery= that.delivery
        that.$http({
          url: that.$api.orderBuyNow,
          data: data,
          cb: (err, res) => {
            if(!err && res.code === 1) {
              that.detail= res.data
              if(res.data.extract_shop) {
                this.shop_id= res.data.extract_shop.shop_id
              }
            } else if(res.code === 0 || res.code === -1 && res.msg) {
              uni.showToast({
                title: res.msg,
                icon: 'none'
              })
            } else {
              uni.showToast({
                title: '订单数据生成失败',
                icon: 'none'
              })
            }
          }
        })
      },
      goDetail(item) {
        uni.navigateTo({
          url: '../components/goodDetail?goods_id=' + this.option.goods_id + '&panic=true' + '&title=' + this.option.title
        })
      }
    }
	}
